.head{
  h1{
    font-size: 2rem;
    margin-top: 15px;
    margin-left: 15px;
  }
  ol{
    padding: 0;
    margin-left: 15px;
    li{
      display: inline-block;
      margin-right: 10px;
    }
  }
}

.timer{
  text-align: center;
  h2{
    font-size: 1.5rem;
    margin: 10px;
  }
  .set_time{
    #week, #hour, #min{
      display: inline-block;
      margin: 5px;
      select{
        width: 115px;
        padding: 5px;
        font-size: 1rem;
      }
    }
    #change_time{
      display: inline-block;
      margin: 4px;
      font-size: 1rem;
      width: 70px;
      padding: 1px;
    }
    p{
      font-size: 1.5rem;
      margin: 3px;
    }
    #put_timer{
      input{
        display: inline-block;
        margin: 4px;
        font-size: 1rem;
        width: 70px;
        padding: 2px;
      }
    }
  }
  .seted_timer{
    margin: 10px;
    font-size: 1rem;
    p{
      display: inline-block;
      margin: 4px;
    }
  }
}

.check_time{
  text-align: center;
  margin-top: 20px;
  h2{
    margin: 5px;
  }
  #button_box{
    margin: 5px;
  }
  #table{
    display: inline-block;
    table{
    border-collapse: collapse;
    border: solid;
      th,td{
        border: solid;
        padding: 10px;
      }
    }
  } 
}